import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Label, Button, Col, Row, Input } from 'reactstrap';

class RecoverUsername extends Component {

  render() {
    return (     
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-password pr-1"></i>Lấy lại tên đăng nhập</strong>
              </CardHeader>
              <CardBody>                
                <Row className="form-group">
                  <Col md="3">
                    <Label>Địa chỉ Email</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <Input type="text" placeholder="Nhập vào địa chỉ email" autoComplete="email" />                    
                    <p className="password-recover-text">Tên đăng nhập sẽ được gửi tới địa chỉ email đăng ký tài khoản của bạn</p>
                  </Col>
                </Row>                
              </CardBody>
            </Card>
            <Row className="text-center margin-bottom-20">
              <Col md="6">
                <Button className="pull-left" color="success" block>Lấy lại tên đăng nhập</Button>
              </Col>
              <Col md="6">
                <Button className="pull-left" color="default" block type="reset">Làm lại</Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    );
  }
}

export default RecoverUsername;
