import React, { Component } from 'react';
import { Row, Col, Button, Card, CardBody, Form, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';

class Register extends Component {
  render() {
    return (
      <Card>
        <CardBody>
          <Form>
            <div class="register-title"><i className="icon-info pr-1"></i>Tạo mới tài khoản</div>
            <InputGroup className="mb-3">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>
                  <i className="icon-user"></i>
                </InputGroupText>
              </InputGroupAddon>
              <Input type="text" placeholder="Tên đăng nhập" autoComplete="username" />
            </InputGroup>
            <InputGroup className="mb-3">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>
                  <i className="icon-lock"></i>
                </InputGroupText>
              </InputGroupAddon>
              <Input type="password" placeholder="Mật khẩu" autoComplete="new-password" />
            </InputGroup>
            <InputGroup className="mb-4">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>
                  <i className="icon-lock"></i>
                </InputGroupText>
              </InputGroupAddon>
              <Input type="password" placeholder="Nhập lại mật khẩu" autoComplete="new-password" />
            </InputGroup>
            <InputGroup className="mb-3">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>N</InputGroupText>
              </InputGroupAddon>
              <Input type="text" placeholder="Tên hiển thị" autoComplete="email" />
            </InputGroup>  
            <InputGroup className="mb-3">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>@</InputGroupText>
              </InputGroupAddon>
              <Input type="text" placeholder="Địa chỉ Email" autoComplete="email" />
            </InputGroup>
            <InputGroup className="mb-3">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>P</InputGroupText>
              </InputGroupAddon>
              <Input type="text" placeholder="Số điện thoại" autoComplete="email" />
            </InputGroup>
            <InputGroup className="mb-3">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>R</InputGroupText>
              </InputGroupAddon>
              <Input type="select" name="selectMulti" id="exampleSelectMulti" placeholder="Quyền hệ thống">
                <option>Nhân viên</option>
                <option>Giám đốc</option>
                <option>Quản lý</option>
              </Input>
            </InputGroup>
            <Row className="text-center margin-bottom-20">
              <Col md="6">
                <Button className="pull-left" color="success" block>Tạo mới</Button>
              </Col>
              <Col md="6">
                <Button className="pull-left" color="default" block type="reset">Làm lại</Button>
              </Col>
            </Row>
          </Form>
        </CardBody>
      </Card>
    );
  }
}

export default Register;
