import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Pagination, PaginationItem, PaginationLink, Button, Badge, Card, CardBody, CardHeader, Col, Row, Table, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { searchUsers, assignUserToRoles, lockUser } from '../../actions/User';
import { connect } from "react-redux";
import { getPaginationInfo, getPagingInfo, moreNextPage, morePrevPage, nextPage, previousPage, selectPage } from '../ViewHelper';
import { ACTION_BUTTON_TYPES } from '../../constants/AppConstant';
import ActionButtons from '../../components/ActionButtons';

function UserRow(props) {
  const user = props.user;
  const order = props.order;
  const userLink = `/users/${user.userName}`;
  const roles = user.roles ? user.roles.split(',') : [];
  const getRole = (role) => {
    return role === 'admin' ? 'success' : 'danger';
  }

  const getLock = (status) => {
    return status ? 'secondary' : 'warning';
  }

  let buttons = [{
    type: ACTION_BUTTON_TYPES.VIEW.type,
    color: ACTION_BUTTON_TYPES.VIEW.color,
    icon: ACTION_BUTTON_TYPES.VIEW.icon,
    text: ACTION_BUTTON_TYPES.VIEW.text,
    url: '',
    action: () => props.view(user)
  }, {
    type: ACTION_BUTTON_TYPES.EDIT.type,
    color: ACTION_BUTTON_TYPES.EDIT.color,
    icon: ACTION_BUTTON_TYPES.EDIT.icon,
    text: ACTION_BUTTON_TYPES.EDIT.text,
    url: '',
    action: () => props.edit(user)
  }, {
    type: ACTION_BUTTON_TYPES.CHANGEROLE.type,
    color: ACTION_BUTTON_TYPES.CHANGEROLE.color,
    icon: ACTION_BUTTON_TYPES.CHANGEROLE.icon,
    text: ACTION_BUTTON_TYPES.CHANGEROLE.text,
    url: '',
    action: () => props.changeRole(user)
  }];

  let extraButtons = [];
  if (user.isLocked) {
    extraButtons.push({
      type: ACTION_BUTTON_TYPES.UNLOCK.type,
      color: ACTION_BUTTON_TYPES.UNLOCK.color,
      icon: ACTION_BUTTON_TYPES.UNLOCK.icon,
      text: ACTION_BUTTON_TYPES.UNLOCK.text,
      url: '',
      action: () => props.unLock(user)
    });
  } else {
    extraButtons.push({
      type: ACTION_BUTTON_TYPES.LOCK.type,
      color: ACTION_BUTTON_TYPES.LOCK.color,
      icon: ACTION_BUTTON_TYPES.LOCK.icon,
      text: ACTION_BUTTON_TYPES.LOCK.text,
      url: '',
      action: () => props.lock(user)
    })
  }
  buttons = buttons.concat(extraButtons);

  return (
    <tr>
      <td><Link to={userLink}>{order}</Link></td>
      <td><Link to={userLink}>{user.userName}</Link></td>
      <td>{roles.map(role => <Badge key={role} color={getRole(role)}>{role}</Badge>)}</td>
      <td><Badge color={getLock(user.isLocked)}>{user.isLocked ? 'Locked' : 'Active'}</Badge></td>

      <td className="text-right action-buttons view-edit-buttons">
        <div className="pull-right">
          <ActionButtons buttons={buttons} />
        </div>
      </td>
    </tr>
  )
}

const padding = 3;

class DataView extends Component {
  state = {
    pagination: {
      prevEnabled: false,
      nextEnabled: false,
      startPage: 0,
      endPage: 0,
      moreNextPage: false,
      morePrevPage: false,
      pages: [1],
      details: {
        page: 1,
        totalPages: 1,
        pageSize: 50
      }
    }
  }

  componentDidMount() {
    this.props.searchUsers(this.props.filter);
  }

  view = (user) => {
    window.location.href = '/user/detail/' + user.userId;
  }

  edit = (user) => {
    window.location.href = '/user/' + user.userId;
  }

  changeRole = (user) => {
    window.location.href = '/user/change-role/' + user.userId;
  }

  unLock = (user) => {
    this.setState({ showConfirmUnlockPopup: true, userLockUnlock: { ...user, isLocked: false } });
  }

  lock = (user) => {
    this.setState({ showConfirmLockPopup: true, userLockUnlock: { ...user, isLocked: true } });
  }

  lockUnlockUser = (status, user) => {
    if (status) {
      this.props.lockUser(user);
    } else {
      this.props.unLockUser(user);
    }
  }

  render() {
    const users = (this.props.paging ? this.props.paging.records : []) || [];
    let pagination = getPaginationInfo(getPagingInfo(this.props.paging), padding);

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={12}>
            <Card>
              <CardHeader>
                <Row>
                  <Col md="4">
                    <i className="fa fa-align-justify"></i> Danh sách tài khoản<small className="text-muted"></small>
                  </Col>
                  <Col md="4">
                  </Col>
                  <Col md="4">
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-magnifier"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="Tìm theo tên hiển thị" autoComplete="keysearch" />
                    </InputGroup>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">STT</th>
                      <th scope="col">Tên đăng nhập</th>
                      <th scope="col">Quyền hệ thống</th>
                      <th scope="col">Trạng thái</th>
                      <th scope="col"></th>
                    </tr>
                  </thead>
                  <tbody>
                    {users.map((user, index) =>
                      <UserRow key={index} view={this.view} edit={this.edit} lock={this.lock} unLock={this.unLock}
                        user={user} order={index + 1} />
                    )}
                  </tbody>
                </Table>
                <Pagination className="pull-right pagination-bottom">
                  {pagination.prevEnabled ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => previousPage(pagination, this.props.paging, this.props.filter, this.props.searchUsers)} previous tag="button" />
                    </PaginationItem>) : (null)
                  }
                  {pagination.morePrevPage ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => morePrevPage(pagination, this.props.paging, (p) => {
                        this.setState({ pagination: p });
                      })} tag="button">...</PaginationLink>
                    </PaginationItem>) : (null)
                  }
                  {
                    pagination.pages.map(p => (
                      <PaginationItem className={p == pagination.details.page ? 'active' : ''}>
                        <PaginationLink onClick={() => selectPage(p, this.state.paging, this.props.pagination, this.props.filter, this.props.searchUsers)} tag="button">
                          {p}
                        </PaginationLink>
                      </PaginationItem>
                    ))
                  }
                  {pagination.moreNextPage ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => moreNextPage(this.statee.pagination, this.props.padding, (p) => {
                        this.setState({ pagination: p });
                      })} tag="button">...</PaginationLink>
                    </PaginationItem>) : (null)
                  }
                  {pagination.nextEnabled ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => nextPage(pagination, this.props.paging, this.props.filter, this.props.searchUsers)} next tag="button" />
                    </PaginationItem>) : (null)
                  }
                </Pagination>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

const mapStateToProps = ({ user }) => {
  return { ...user }
};

const Users = connect(mapStateToProps, {
  searchUsers,
  assignUserToRoles,
  lockUser
})(DataView);

export default Users;
