import React from 'react';
import { Link } from 'react-router-dom';
import { Col, Row, Button } from 'reactstrap';
import { ACTION_BUTTON_TYPES } from '../constants/AppConstant';

export default class ActionButtons extends React.Component {
  render() {
    let newNum = 0;
    if (this.props.bottomButtons) {
      newNum = 12 / this.props.bottomButtons.length;
    }

    return (
      <div>
        {
          this.props.buttons && this.props.buttons.map((bb, i) => {
            return (
              <Button key={i} color={bb.color} onClick={() => bb.action()}>
                <i className={bb.icon}></i>&nbsp;{bb.text}
              </Button>
            )
          })
        }        
        {
          this.props.bottomButtons &&
          <Row className="action-buttons-bottom-box">
            {this.props.bottomButtons && this.props.bottomButtons.map((b, i) => {
              // eslint-disable-next-line default-case
              switch (b.type) {
                case ACTION_BUTTON_TYPES.SAVE:
                  return (
                    <Col key={i} md={newNum}>
                      <Button className="pull-left" color="success" onClick={() => b.action()} block>
                        <i className="icon-cloud-download"></i>&nbsp;
                        Lưu
                      </Button>
                    </Col>
                  )
                case ACTION_BUTTON_TYPES.REFRESH:
                  return (
                    <Col key={i} md={newNum}>
                      <Button className="pull-left" color="info" onClick={() => b.action()} block type="reset">
                        <i className="icon-refresh"></i>&nbsp;
                        Làm mới
                      </Button>
                    </Col>
                  )
                case ACTION_BUTTON_TYPES.BACK:
                  return (
                    <Col key={i} md={newNum}>
                      <Link to={b.url} className="btn btn-default btn-block pull-left">
                        <i className="icon-action-undo"></i>&nbsp;
                        Quay lại
                      </Link>
                    </Col>
                  )
              }
            })}
          </Row>
        }
      </div>
    );
  }
}
