export function getPagingInfo(paging) {
  var p = {
    page: 1,
    totalPages: 1,
    pageSize: 50
  };
  if (paging) {
    var page = parseInt(paging.offset / paging.limit) + 1;
    p = {
      page: page,
      totalPages: paging.totalPages,
      pageSize: paging.limit
    };
  }

  return p;
}

export function getPaginationInfo(p, padding) {
  var prevEnabled = p.page > 1;
  var nextEnabled = p.page < p.totalPages;
  var span = padding;
  var startPage = p.page - span;
  var endPage = startPage + (span * 2 + 1);

  if (startPage < 1) {
    startPage = 1;
    endPage = startPage + (span * 2 + 1);
    endPage = endPage > p.totalPages ? p.totalPages : endPage;
  }

  if (endPage > p.totalPages) {
    endPage = p.totalPages;
    startPage = endPage - (span * 2 + 1);
    startPage = startPage < 1 ? 1 : startPage;
  }

  var moreNextPage = endPage < p.totalPages;
  var morePrevPage = startPage > 1;
  var details = p;
  var pages = new Array(endPage - startPage + 1);

  for (var i = 0; i < pages.length; ++i) {
    pages[i] = startPage + i;
  }

  return {
    prevEnabled,
    nextEnabled,
    startPage,
    endPage,
    details,
    moreNextPage,
    morePrevPage,
    pages
  };
}

export function previousPage(pagination, paging, filter, searchUsers) {
  var offset = (pagination.details.page - 1 - 1) * paging.limit;
  searchUsers({
    ...filter,
    offset: offset
  });
}

export function nextPage(pagination, paging, filter, searchUsers) {
  var offset = (pagination.details.page + 1 - 1) * paging.limit;
  searchUsers({
    ...filter,
    offset: offset
  });
}

export function selectPage(page, paging, pagination, filter, searchUsers) {
  if (pagination.details.page === page) {
    return;
  }
  var offset = (page - 1) * paging.limit;
  searchUsers({
    ...filter,
    offset: offset
  });
}

export function morePrevPage(pagination, padding, callback) {
  var p = {
    ...pagination
  };
  var span = padding * 2 + 1;
  var startPage = p.startPage - span;
  startPage = startPage > 0 ? startPage : 1;
  var endPage = startPage + span;
  endPage = endPage > p.totalPages ? p.totalPages : endPage;
  var moreNextPage = endPage < p.totalPages;
  var morePrevPage = startPage > 1;
  var pages = new Array(endPage - startPage + 1);
  for (var i = 0; i < pages.length; ++i) {
    pages[i] = startPage + i;
  }

  p.startPage = startPage;
  p.endPage = endPage;
  p.moreNextPage = moreNextPage;
  p.morePrevPage = morePrevPage;
  p.pages = pages;

  callback(p);
}

export function moreNextPage(pagination, padding, callback) {
  var p = {
    ...pagination
  };
  var span = padding * 2 + 1;
  var endPage = p.endPage + span;
  endPage = endPage > p.totalPages ? p.totalPages : endPage;
  var startPage = endPage - span;
  startPage = startPage > 0 ? startPage : 1;
  var moreNextPage = endPage < p.totalPages;
  var morePrevPage = startPage > 1;
  var pages = new Array(endPage - startPage + 1);
  for (var i = 0; i < pages.length; ++i) {
    pages[i] = startPage + i;
  }

  p.startPage = startPage;
  p.endPage = endPage;
  p.moreNextPage = moreNextPage;
  p.morePrevPage = morePrevPage;
  p.pages = pages;

  callback();
}
