import React, { Component, Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';
import { Container } from 'reactstrap';
import { NavLink } from "react-router-dom";
import {
  AppAside,
  AppFooter,
  AppHeader,
  AppSidebar,
  AppSidebarFooter,
  AppSidebarForm,
  AppSidebarHeader,
  AppSidebarMinimizer,
  AppSidebarNav
} from '@coreui/react';
// sidebar nav config
import navigation from '../../_nav';
// routes config
import routes from '../../routes';
import { signOut } from '../../helpers';

const DefaultFooter = React.lazy(() => import('./DefaultFooter'));
const DefaultHeader = React.lazy(() => import('./DefaultHeader'));


class DefaultLayout extends Component {
  state = {
    open: "",
    numberClick: 0
  }

  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  openNav = (key) => {
    if (this.state.numberClick == 1) {
      this.setState({ open: "", numberClick: 0 });
    } else {
      this.setState({ open: key, numberClick: 1 });
    }
  }

  render() {
    var { match } = this.props;
    return (
      <div className="app">
        <AppHeader fixed>
          <Suspense fallback={this.loading()}>
            <DefaultHeader onLogout={() => signOut()} />
          </Suspense>
        </AppHeader>
        <div className="app-body">
          <AppSidebar fixed display="lg">
            <AppSidebarHeader />
            <AppSidebarForm />
            <Suspense>
              <AppSidebarNav>
                <ul className="nav">
                  {
                    navigation.items.map((item, index) =>
                      <li key={index} className={item.children ? "nav-item nav-dropdown " + (this.state.open == item.key ? "open" : "") : "nav-item"}
                        onClick={() => this.openNav(item.key)}>
                        {
                          item.action ? (
                            <div onClick={() => item.action()} className="nav-link">
                              <i className={"nav-icon " + item.icon}></i>
                              {item.name}
                            </div>
                          )
                          :
                          (
                            <NavLink to={item.children ? "#" : item.url} className={item.children ? "nav-link nav-dropdown-toggle" : "nav-link"}>
                              <i className={"nav-icon " + item.icon}></i>{item.name}
                            </NavLink>
                          )}
                        <ul className="nav-dropdown-items">
                          {
                            item.children ? (
                              item.children.map((c, i) => {
                                return (<li key={i} className="nav-item">
                                  <a className="nav-link" href={c.url}>
                                    <i className={"nav-icon " + c.icon}></i> {c.name}</a>
                                </li>)
                              })
                            ) : (<div></div>)
                          }
                        </ul>
                      </li>)
                  }
                </ul>
              </AppSidebarNav>
            </Suspense>
            <AppSidebarFooter />
            <AppSidebarMinimizer />
          </AppSidebar>
          <main className="main">

            <Container fluid style={{ marginTop: "20px" }}>
              <Suspense fallback={this.loading()}>
                <Switch>
                  {routes.map((route, idx) => <Route
                    key={idx}
                    path={`${match.url}${route.path}`}
                    exact={route.exact}
                    component={route.component} />
                  )}
                </Switch>
              </Suspense>
            </Container>
          </main>
        </div>
        <AppFooter>
          <Suspense fallback={this.loading()}>
            <DefaultFooter />
          </Suspense>
        </AppFooter>
      </div>
    );
  }
}

export default DefaultLayout;
