import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardHeader, Col, Row, Label } from 'reactstrap';
import { connect } from "react-redux";
import {
  loadRole
} from '../../actions/Role';
import Api from '../../apis/Api';

class DataView extends Component {
  constructor(props) {
    super(props);

    this.state = { role: {} };

    let id = props.match.params.id;
    Api().get('role/' + id).then(response => {
      this.setState({ role: response.data });
    });
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>Chi tiết mẫu</strong>
              </CardHeader>
              <CardBody>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Mã </Label>
                  </Col>
                  <Col xs="12" md="3">
                    <p class="form-control-static">{this.state.role.key}</p>
                  </Col>
                  <Col md="3">
                    <Label>Tên </Label>
                  </Col>
                  <Col xs="12" md="3">
                    <p class="form-control-static">{this.state.role.name}</p>
                  </Col>
                  <Col md="3">
                    <Label>Giá trị </Label>
                  </Col>
                  <Col xs="12" md="3">
                    <p class="form-control-static">{this.state.role.value}</p>
                  </Col>
                </Row>
              </CardBody>
            </Card>
            <Row className="margin-bottom-20 action-buttons view-edit-buttons">
              <Col md="4">
                <Button className="pull-left" color="success" onClick={() => { window.location.href = '/role/' + this.state.role.id }} block>
                  <i className="icon-cloud-download"></i>&nbsp;
                  Cập nhật
                </Button>
              </Col>
              <Col md="4">
                <Button className="pull-left" color="danger" block type="reset">
                  <i className="icon-trash"></i>&nbsp;
                  Xóa
                </Button>
              </Col>
              <Col md="4">
                <Link to="/roles" className="btn btn-default btn-block pull-left">
                  <i className="icon-action-undo"></i>&nbsp;
                  Quay lại
                </Link>
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    )
  }
}

const mapStateToProps = ({ role }) => {
  return { ...role };
};

const RoleDetail = connect(mapStateToProps, {
  loadRole
})(DataView);

export default RoleDetail;
