import React, { Component } from 'react';
import { Input, Button, Card, CardBody, CardHeader, Col, Row, Label } from 'reactstrap';

class RecoverPassword extends Component {

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-password pr-1"></i>Lấy lại mật khẩu</strong>
              </CardHeader>
              <CardBody>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Tên đăng nhập</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <Input type="text" placeholder="Tên đăng nhập" autoComplete="username" />
                  </Col>
                </Row>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Địa chỉ Email</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <Input type="text" placeholder="Địa chỉ Email" autoComplete="email" />
                    <p className="password-recover-text">Mật khẩu mới được gửi tới địa chỉ email của bạn</p>
                  </Col>
                </Row>                
              </CardBody>
            </Card>
            <Row className="text-center margin-bottom-20">
              <Col md="6">
                <Button className="pull-left" color="success" block>Lấy lại mật khẩu</Button>
              </Col>
              <Col md="6">
                <Button className="pull-left" color="default" block type="reset">Làm lại</Button>
              </Col>
            </Row>
            <Row className="text-right">
              <Col md="12" className="user-recover">
                <a href="/user/recover">Lấy lại tên đăng nhập ?</a>
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    )
  }
}

export default RecoverPassword;
