
import Register from './views/Pages/Register';
import Users from './views/Users/Users';
import UserDetail from './views/Users/UserDetail';
import User from './views/Users/User';
import RecoverUsername from './views/Users/RecoverUsername';
import RecoverPassword from './views/Password/RecoverPassword';
import UpdatePassword from './views/Password/UpdatePassword';
import Role from './views/Role/Role';
import Roles from './views/Role/Roles';
import RoleDetail from './views/Role/RoleDetail';
import Setting from './views/Setting/Setting';
import Settings from './views/Setting/Settings';
import SettingDetail from './views/Setting/SettingDetail';

// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '', exact: true, name: 'Home', component: Users },
  { path: 'register', exact: true, name: 'Đăng ký', component: Register},
  { path: 'users', exact: true,  name: 'Danh sách người dùng', component: Users },
  { path: 'user/detail/:id', exact: true, name: 'Thông tin người dùng', component: UserDetail },
  { path: 'user/:id', exact: true, name: 'Người dùng', component: User },
  { path: 'password/recover', exact: true, name: 'Recover Password', component: RecoverPassword },
  { path: 'username/recover', exact: true, name: 'Recover Username', component: RecoverUsername },
  { path: 'password/update', exact: true, name: 'Thay đổi mật khẩu', component: UpdatePassword },
  { path: 'role', exact: true, name: 'Thay đổi phân quyền', component: Role },
  { path: 'role/:id', exact: true, name: 'Thay đổi phân quyền', component: Role },
  { path: 'roles', exact: true, name: 'Danh sách phân quyền', component: Roles },
  { path: 'role/detail/:id', exact: true, name: 'Thông tin phân quyền', component: RoleDetail },
  { path: 'setting', exact: true, name: 'Cài đặt', component: Setting },
  { path: 'setting/:id', exact: true, name: 'Cài đặt', component: Setting },
  { path: 'settings', exact: true, name: 'Danh sách tham số', component: Settings },
  { path: 'setting/detail/:id', exact: true, name: 'Thông tin tham số', component: SettingDetail }
];

export default routes;
