import React, { Component } from 'react';
import { Input, Button, Card, CardBody, CardHeader, Col, Row, Label } from 'reactstrap';

class UpdatePassword extends Component {

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-password pr-1"></i>Cập nhật mật khẩu truy cập</strong>
              </CardHeader>
              <CardBody>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Mật khẩu hiện tại</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <Input type="password" placeholder="Nhập mật khẩu hiện tại" autoComplete="password" />
                  </Col>
                </Row>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Mật khẩu mới</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <Input type="password" placeholder="Nhập mật khẩu mới" autoComplete="password" />
                  </Col>
                </Row>   
                <Row className="form-group">
                  <Col md="3">
                    <Label>Nhập lại mật khẩu mới</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <Input type="password" placeholder="Nhập lại mật khẩu mới" autoComplete="password" />
                  </Col>
                </Row>       
              </CardBody>
            </Card>
            <Row className="text-center margin-bottom-20">
              <Col md="6">
                <Button className="pull-left" color="success" block>Đổi mật khẩu</Button>
              </Col>
              <Col md="6">
                <Button className="pull-left" color="default" block type="reset">Quay lại</Button>
              </Col>
            </Row>            
          </Col>
        </Row>
      </div>
    )
  }
}

export default UpdatePassword;
