import React, { Component } from 'react';
import { Button, Card, CardBody, CardHeader, Col, Row, Label } from 'reactstrap';

class UserDetail extends Component {

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>Chi tiết tài khoản</strong>
              </CardHeader>
              <CardBody>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Tên đăng nhập</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <p class="form-control-static">nhttrang</p>
                  </Col>
                </Row>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Tên hiển thị</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <p class="form-control-static">Nguyễn Thị Tú Trang</p>
                  </Col>
                </Row>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Địa chỉ Email</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <p class="form-control-static">nguyentutrang@gmail.com</p>
                  </Col>
                </Row>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Số điện thoại</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <p class="form-control-static">091238921389</p>
                  </Col>                  
                </Row>
                <Row className="form-group">
                  <Col md="3">
                    <Label>Quyền hệ thống</Label>
                  </Col>
                  <Col xs="12" md="9">
                    <p class="form-control-static">HC-NS: Account</p>
                  </Col>
                </Row>
              </CardBody>
            </Card>
            <Row className="text-center margin-bottom-20">
              <Col md="4">
                <Button className="pull-left" color="success" block>Cập nhật</Button>
              </Col>
              <Col md="4">
                <Button className="pull-left" color="default" block type="reset">Quay lại</Button>
              </Col>
              <Col md="4">
                <Button className="pull-left" color="danger" block type="reset">Xóa</Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    )
  }
}

export default UserDetail;
