import React, { Component } from 'react';
import { Card, CardBody, Button, Col, Row, Form, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';

class User extends Component {
  render() {
    return (     
        <Card>
          <CardBody>
            <Form>
              <strong><i className="icon-info pr-1"></i>Cập nhật tài khoản</strong>
              <InputGroup className="mb-3 margin-top-10">
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <i className="icon-user"></i>
                  </InputGroupText>
                </InputGroupAddon>
                <Input type="text" placeholder="Tên đăng nhập" autoComplete="username" />
              </InputGroup>              
              <InputGroup className="mb-3">
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>N</InputGroupText>
                </InputGroupAddon>
                <Input type="text" placeholder="Tên hiển thị" autoComplete="email" />
              </InputGroup>  
              <InputGroup className="mb-3">
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>@</InputGroupText>
                </InputGroupAddon>
                <Input type="text" placeholder="Địa chỉ Email" autoComplete="email" />
              </InputGroup>
              <InputGroup className="mb-3">
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>P</InputGroupText>
                </InputGroupAddon>
                <Input type="text" placeholder="Số điện thoại" autoComplete="email" />
              </InputGroup>
              <InputGroup className="mb-3">
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>R</InputGroupText>
                </InputGroupAddon>
                <Input type="select" name="selectMulti" id="exampleSelectMulti" placeholder="Quyền hệ thống">
                  <option>Nhân viên</option>
                  <option>Giám đốc</option>
                  <option>Quản lý</option>
                </Input>
              </InputGroup>
              <Row className="text-center margin-bottom-20">
                <Col md="6">
                  <Button className="pull-left" color="success" block>Cập nhật</Button>
                </Col>
                <Col md="6">
                  <Button className="pull-left" color="default" block type="reset">Hủy cập nhật</Button>
                </Col>
              </Row>
            </Form>
          </CardBody>
        </Card>
    );
  }
}

export default User;
