import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Table, Pagination, PaginationItem, PaginationLink, Input, InputGroup, InputGroupAddon, InputGroupText, Button, Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import { connect } from "react-redux";
import {
  selectAllSettings,
  unselectAllSettings,
  selectSetting,
  loadSettings,
  loadSetting
} from "../../actions/Setting";
import { getPaginationInfo, getPagingInfo, moreNextPage, morePrevPage, nextPage, previousPage, selectPage } from '../ViewHelper';
import { ACTION_BUTTON_TYPES } from '../../constants/AppConstant';
import ActionButtons from '../../components/ActionButtons';

function SettingRow(props) {
  const setting = props.setting;

  let buttons = [{
    type: ACTION_BUTTON_TYPES.VIEW.type,
    color: ACTION_BUTTON_TYPES.VIEW.color,
    icon: ACTION_BUTTON_TYPES.VIEW.icon,
    text: ACTION_BUTTON_TYPES.VIEW.text,
    url: '',
    action: () => props.view(setting)
  },{
    type: ACTION_BUTTON_TYPES.EDIT.type,
    color: ACTION_BUTTON_TYPES.EDIT.color,
    icon: ACTION_BUTTON_TYPES.EDIT.icon,
    text: ACTION_BUTTON_TYPES.EDIT.text,
    url: '',
    action: () => props.edit(setting)
  },{
    type: ACTION_BUTTON_TYPES.DELETE.type,
    color: ACTION_BUTTON_TYPES.DELETE.color,
    icon: ACTION_BUTTON_TYPES.DELETE.icon,
    text: ACTION_BUTTON_TYPES.DELETE.text,
    url: '',
    action: () => props.showDeleteModal(setting)
  }];

  return (
    <tr key={setting.id.toString()}>
      <td>{setting.id}</td>
      <td>{setting.key}</td>
      <td>{setting.name}</td>
      <td>{setting.value}</td>

      <td className="text-right action-buttons view-edit-buttons">
        <div className="pull-right">
          <ActionButtons buttons={buttons} />
        </div>
      </td>
    </tr>
  )
}

const padding = 3;
class DataView extends Component {
  state = {
    pagination: {
      prevEnabled: false,
      nextEnabled: false,
      startPage: 0,
      endPage: 0,
      moreNextPage: false,
      morePrevPage: false,
      pages: [1],
      details: {
        page: 1,
        totalPages: 1,
        pageSize: 50
      }
    },
    showDeleteModal: false
  }

  componentDidMount() {
    this.props.loadSettings(this.props.settingFilter);
  }

  deleteSetting = () => {
    this.props.deleteSetting(this.state.selectedSetting);
  }

  view = (setting) => {
    window.location.href = '/setting/detail/' + setting.id;
  }

  edit = (setting) => {
    window.location.href = '/setting/' + setting.id;
  }

  render() {
    const settings = (this.props.settings ? this.props.settings.records : []) || [];
    this.state.pagination = getPaginationInfo(getPagingInfo(this.props.paging), padding);
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-password pr-1"></i>Danh sách mẫu</strong>
              </CardHeader>
              <CardBody>
                <fieldset className="card-fieldset margin-bottom-20">
                  <legend className="card-legend">Thông tin chung</legend>
                  <Row>
                    <Col md="10">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-magnifier"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Nhập thông tin cần tìm" autoComplete="keysearch" />
                      </InputGroup>
                    </Col>
                    <Col md="2">
                      <Button className="pull-left" color="primary" block>Tìm</Button>
                    </Col>
                  </Row>
                </fieldset>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Số thứ tự</th>
                      <th scope="col">Mã</th>
                      <th scope="col">Tên</th>
                      <th scope="col">Giá trị</th>
                      <th scope="col">&nbsp;</th>
                    </tr>
                  </thead>
                  <tbody>
                    {settings.map((setting, index) =>
                      <SettingRow key={index} setting={setting} edit={this.edit} view={this.view} showDeleteModal={() => { this.setState({ showDeleteModal: true, selectedSetting: setting }) }} order={index + 1} />
                    )}
                  </tbody>
                </Table>
                <Pagination className="pull-right pagination-bottom">
                  {this.state.pagination.prevEnabled ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => previousPage(this.state.pagination, this.props.paging, this.props.filter, this.props.searchUsers)} previous tag="button" />
                    </PaginationItem>) : (null)
                  }
                  {this.state.pagination.morePrevPage ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => morePrevPage(this.state.pagination, this.props.paging, (p) => {
                        this.setState({ pagination: p });
                      })} tag="button">...</PaginationLink>
                    </PaginationItem>) : (null)
                  }
                  {
                    this.state.pagination.pages.map(p => (
                      <PaginationItem key={p} className={p == this.state.pagination.details.page ? 'active' : ''}>
                        <PaginationLink onClick={() => selectPage(p, this.state.paging, this.props.pagination, this.props.filter, this.props.searchUsers)} tag="button">
                          {p}
                        </PaginationLink>
                      </PaginationItem>
                    ))
                  }

                  {this.state.pagination.moreNextPage ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => moreNextPage(this.state.pagination, this.props.padding, (p) => {
                        this.setState({ pagination: p });
                      })} tag="button">...</PaginationLink>
                    </PaginationItem>) : (null)
                  }
                  {this.state.pagination.nextEnabled ?
                    (<PaginationItem>
                      <PaginationLink onClick={() => nextPage(this.state.pagination, this.props.paging, this.props.filter, this.props.searchUsers)} next tag="button" />
                    </PaginationItem>) : (null)
                  }
                </Pagination>
              </CardBody>
            </Card>
          </Col>
        </Row>
        {this.state.showDeleteModal ? (
          <div className="modal fade show" id="dangerModal" role="dialog" aria-labelledby="myModalLabel" style={{ display: 'block' }}>
            <div className="modal-dialog modal-danger" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h4 className="modal-title">Công ty</h4>
                  <button className="close" type="button" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">×</span>
                  </button>
                </div>
                <div className="modal-body">
                  <p>Bạn có muốn xóa công ty {this.state.settingDeleteName} này không?</p>
                </div>
                <div className="modal-footer">
                  <button className="btn btn-secondary" type="button" data-dismiss="modal" onClick={this.setState({ showDeleteModal: false })}>Không</button>
                  <button className="btn btn-danger" type="button" onClick={() => this.deleteSetting()}>Có</button>
                </div>
              </div>
            </div>
          </div>
        ) : ''}
      </div>
    )
  }
}

const mapStateToProps = ({ setting }) => {
  return { ...setting };
};

const Settings = connect(mapStateToProps, {
  selectAllSettings,
  unselectAllSettings,
  selectSetting,
  loadSettings,
  loadSetting
})(DataView);

export default Settings;
