import React, { Component } from 'react';
import { Button, Card, CardBody, CardHeader, Col, Row, Label } from 'reactstrap';

class SettingDetail extends Component {
  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>Chi tiết cấu hình</strong>
              </CardHeader>
              <CardBody>
                <fieldset className="card-fieldset">
                  <legend className="card-legend">Thông tin chung</legend>
                  <Row>
                    <Col md="12">
                      <Row className="form-group">
                        <Col md="3">
                          <Label>Mã tham số</Label>
                        </Col>
                        <Col xs="12" md="9">
                          <p className="form-control-static">CMND/CCCD</p>
                        </Col>
                      </Row>
                    </Col>
                    <Col md="12">
                      <Row className="form-group">
                        <Col md="3">
                          <Label>Giá trị</Label>
                        </Col>
                        <Col xs="12" md="9">
                          <p className="form-control-static">083218213</p>
                        </Col>
                      </Row>
                    </Col>                    
                  </Row>
                </fieldset>                
              </CardBody>
            </Card>
            <Row className="text-center margin-bottom-20">
              <Col md="4">
                <Button className="pull-left" color="success" block>Cập nhật</Button>
              </Col>
              <Col md="4">
                <Button className="pull-left" color="danger" block type="reset">Xóa</Button>
              </Col>
              <Col md="4">
                <Button className="pull-left" color="default" block type="reset">Quay lại</Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    )
  }
}

export default SettingDetail;
