import React, { Component } from 'react';
import { Input, Card, CardBody, CardHeader, Col, Row, Label } from 'reactstrap';
import "react-datepicker/dist/react-datepicker.css";
import { connect } from "react-redux";
import {
  loadSetting,
  selectSetting,
  addSetting,
  addSettingSuccess,
  updateSetting,
  updateSettingSuccess,
  deleteSetting
} from '../../actions/Setting';
import { showLoading } from '../../actions/Common';
import Api from '../../apis/Api';
import { DEFAULT_LANGUAGE, ACTION_BUTTON_TYPES } from '../../constants/AppConstant';
import ActionButtons from '../../components/ActionButtons';

class DataView extends Component {
  constructor(props) {
    super(props);

    this.state = { setting: {} };

    let id = props.match.params.id;
    if (id) {
      Api().get('setting/' + id).then(response => {
        this.setState({ setting: response.data });
      });
    }
  }

  componentWillReceiveProps(newProps) {
    if (newProps.hideLoading == true) {
      window.location.href = '/settings';
    }
  }

  changeSelect = (name, e) => {
    let newState = { ...this.state };
    newState.setting[name] = e.target.value;
    this.setState(newState);
  }

  changeInput = (name, e) => {
    let newState = { ...this.state };
    newState.setting[name] = e.target.value;
    this.setState(newState);
  }

  changeEditor = (name, evt) => {
    let newState = { ...this.state };
    newState.setting[name] = evt.editor.getData();
    this.setState(newState);
  }

  changeDateTime = (name, e) => {
    let newState = { ...this.state };
    newState.setting[name] = e;
    this.setState(newState);
  }

  validate = () => {
    return true;
  }

  save = () => {
    if (this.validate()) {
      this.props.showLoading();
      let setting = { ...this.state.setting };
      if (!setting.languageCode) {
        setting.languageCode = DEFAULT_LANGUAGE;
      }
      if (this.props.match.params.id) {
        this.props.updateSetting({
          model: {
            ...setting
          }
        })
      } else {
        this.props.addSetting({
          model: {
            ...setting
          }
        })
      }
    }
  }

  reset = () => {
    let setting = {};
    Object.keys(this.state.setting).forEach((key) => {
      setting[key] = '';
    });
    this.setState({ setting: setting });
  }

  render() {
    let bottomButtons = [
      {
        type: ACTION_BUTTON_TYPES.SAVE,
        url: '',
        action: () => this.save()
      },
      {
        type: ACTION_BUTTON_TYPES.REFRESH,
        url: '',
        action: () => this.reset()
      },
      {
        type: ACTION_BUTTON_TYPES.BACK,
        url: '/settings',
        action: () => { }
      }
    ];

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader>
                <strong><i className="icon-password pr-1"></i>Tạo mẫu</strong>
              </CardHeader>
              <CardBody>
                <fieldset className="card-fieldset">
                  <legend className="card-legend">Thông tin chung</legend>
                  <Row>
                    <Col md="6">
                      <Row className="form-group">
                        <Col md="3">
                          <Label>Mã </Label>
                        </Col>
                        <Col xs="12" md="9">
                          <Input type="text" value={this.state.setting.key} onChange={(e) => this.changeInput('key', e)} placeholder="Mã">
                          </Input>
                        </Col>
                      </Row>                      
                      <Row className="form-group">
                        <Col md="3">
                          <Label>Giá trị </Label>
                        </Col>
                        <Col xs="12" md="9">
                          <Input type="text" value={this.state.setting.value} onChange={(e) => this.changeInput('value', e)} placeholder="Giá trị">
                          </Input>
                        </Col>
                      </Row>
                    </Col>
                    <Col md="6">                      
                      <Row className="form-group">
                        <Col md="3">
                          <Label>Tên </Label>
                        </Col>
                        <Col xs="12" md="9">
                          <Input type="text" value={this.state.setting.name} onChange={(e) => this.changeInput('name', e)} placeholder="Tên">
                          </Input>
                        </Col>
                      </Row>                      
                    </Col>
                  </Row>
                </fieldset>
              </CardBody>
            </Card>
            <ActionButtons bottomButtons={bottomButtons} />
          </Col>
        </Row>
      </div>
    )
  }
}

const mapStateToProps = ({ setting, common }) => {
  return { ...setting, ...common };
};

const Setting = connect(mapStateToProps, {
  loadSetting,
  selectSetting,
  addSetting,
  addSettingSuccess,
  updateSetting,
  updateSettingSuccess,
  deleteSetting,
  showLoading
})(DataView);

export default Setting;
